.project-create {
  height: 100%;
  padding-top: 10px;
}
.project-create-header {
  position: sticky;
  top: 0;
  background-color: #fff;
  padding: 30px 0;
  margin: 0 20px;
  border-radius: 6px;
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.1);
  overflow-y: hidden;
  transition: all 0.3s;
  z-index: 9;
}
.project-create-header .header-info {
  padding: 0 20px;
}
.project-create-header .header-info h2 {
  font-size: 18px;
  font-weight: 700;
}
.project-create-header .header-info p {
  font-size: 12px;
  color: #9a9ca0;
  font-weight: 400;
}
.project-create-content {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 0 20px;
  max-width: 1020px;
  margin: 20px auto;
  background: #fff;
  box-shadow: 0 2px 3px #eee;
  border-radius: 4px;
  padding: 66px 260px 42px;
}
.project-create-form {
  max-width: 300px;
}
